import React, { useMemo, useRef, useImperativeHandle, forwardRef } from 'react';
import clsx from 'clsx';
import { addProps } from '@element/react-common';
import {
    ElevationPropTypes,
    ElevationDefaultProps
} from './generated/propTypes';

const Elevation = forwardRef((props, externalRef) => {
    const {
        children,
        className,
        customPadding,
        elevation,
        noWrap,
        padding,
        rounded,
        tag,
        outlined,
        style,
        transition,
        ...customProps
    } = props;

    const ref = useRef();

    useImperativeHandle(externalRef, () => ref.current, []);

    const classes = useMemo(
        () =>
            clsx(
                'lmnt',
                `mdc-elevation--z${elevation}`,
                padding === 'standard' && `lmnt-elevation--padding-standard`,
                padding === 'dense' && `lmnt-elevation--padding-dense`,
                padding === 'airy' && `lmnt-elevation--padding-airy`,
                transition && 'mdc-elevation-transition',
                rounded && 'lmnt-elevation--rounded',
                outlined && 'lmnt-elevation--outlined',
                className
            ),
        [elevation, padding, transition, rounded, outlined, className]
    );

    const styles = useMemo(
        () => ({ ...style, padding: customPadding }),
        [style, customPadding]
    );

    const Tag = useMemo(() => tag, [tag]);
    return (
        // eslint-disable-next-line react/jsx-no-useless-fragment
        <>
            {noWrap ? (
                addProps(children, { className: classes, ...customProps })
            ) : (
                <Tag
                    ref={ref}
                    className={classes}
                    style={styles}
                    {...customProps}
                >
                    {children}
                </Tag>
            )}
        </>
    );
});

Elevation.displayName = 'Elevation';
Elevation.propTypes = ElevationPropTypes;
Elevation.defaultProps = ElevationDefaultProps;

export default React.memo(Elevation);
